.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mr-auto
{
  margin-left: 20px;
}


.CountBar
{
  width: 80%;
  height: 70px;
  margin-left: 130px;
  margin-top: 30px;
  padding-left: 15px;
  font-size: 1.5em;
}

.list-group
{
  height: 80px;
}

.difficulty {
  position: relative;
    bottom: -10px;
    left: 75%;
}

